iewBox%3D%220%200%2016%2016%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%20%20%20%20%3Crect%20fill%3D%22%23999%22%20x%3D%223%22%20y%3D%228%22%20width%3D%2210%22%20height%3D%221%22%20%2F%3E%0A%3C%2Fsvg%3E\");\n}\n\n/* Legend\n ========================================================================== */\n/*\n * Legend\n * 1. Behave like block element\n * 2. Correct the color inheritance from `fieldset` elements in IE.\n * 3. Remove padding so people aren't caught out if they zero out fieldsets.\n * 4. Style\n */\n.uk-legend {\n  /* 1 */\n  width: 100%;\n  /* 2 */\n  color: inherit;\n  /* 3 */\n  padding: 0;\n  /* 4 */\n  font-size: 1.5rem;\n  line-height: 1.4;\n}\n\n/* Custom controls\n ========================================================================== */\n/*\n * 1. Container fits its content\n * 2. Create position context\n * 3. Prevent content overflow\n * 4. Behave like most inline-block elements\n */\n.uk-form-custom {\n  /* 1 */\n  display: inline-block;\n  /* 2 */\n  position: relative;\n  /* 3 */\n  max-width: 100%;\n  /* 4 */\n  vertical-align: middle;\n}\n\n/*\n * 1. Position and resize the form control to always cover its container\n * 2. Required for Firefox for positioning to the left\n * 3. Required for Webkit to make `height` work\n * 4. Hide controle and show cursor\n * 5. Needed for the cursor\n * 6. Clip height caused by 5. Needed for Webkit only\n */\n.uk-form-custom select,\n.uk-form-custom input[type=file] {\n  /* 1 */\n  position: absolute;\n  top: 0;\n  z-index: 1;\n  width: 100%;\n  height: 100%;\n  /* 2 */\n  left: 0;\n  /* 3 */\n  -webkit-appearance: none;\n  /* 4 */\n  opacity: 0;\n  cursor: pointer;\n}\n\n.uk-form-custom input[type=file] {\n  /* 5 */\n  font-size: 500px;\n  /* 6 */\n  overflow: hidden;\n}\n\n/* Label\n ========================================================================== */\n.uk-form-label {\n  color: #333;\n  font-size: 0.875rem;\n}\n\n/* Layout\n ========================================================================== */\n/*\n * Stacked\n */\n.uk-form-stacked .uk-form-label {\n  display: block;\n  margin-bottom: 5px;\n}\n\n/*\n * Horizontal\n */\n/* Tablet portrait and smaller */\n@media (max-width: 959px) {\n  /* Behave like `uk-form-stacked` */\n  .uk-form-horizontal .uk-form-label {\n    display: block;\n    margin-bottom: 5px;\n  }\n}\n/* Tablet landscape and bigger */\n@media (min-width: 960px) {\n  .uk-form-horizontal .uk-form-label {\n    width: 200px;\n    margin-top: 7px;\n    float: left;\n  }\n\n  .uk-form-horizontal .uk-form-controls {\n    margin-left: 215px;\n  }\n\n  /* Better vertical alignment if controls are checkboxes and radio buttons with text */\n  .uk-form-horizontal .uk-form-controls-text {\n    padding-top: 7px;\n  }\n}\n/* Icons\n ========================================================================== */\n/*\n * 1. Set position\n * 2. Set width\n * 3. Center icon vertically and horizontally\n * 4. Style\n */\n.uk-form-icon {\n  /* 1 */\n  position: absolute;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  /* 2 */\n  width: 40px;\n  /* 3 */\n  display: inline-flex;\n  justify-content: center;\n  align-items: center;\n  /* 4 */\n  color: #999;\n}\n\n/*\n * Required for `a`.\n */\n.uk-form-icon:hover {\n  color: #666;\n}\n\n/*\n * Make `input` element clickable through icon, e.g. if it's a `span`\n */\n.uk-form-icon:not(a):not(button):not(input) {\n  pointer-events: none;\n}\n\n/*\n * Input padding\n */\n.uk-form-icon:not(.uk-form-icon-flip) ~ .uk-input {\n  padding-left: 40px !important;\n}\n\n/*\n * Position modifier\n */\n.uk-form-icon-flip {\n  right: 0;\n  left: auto;\n}\n\n.uk-form-icon-flip ~ .uk-input {\n  padding-right: 40px !important;\n}\n\n/* ========================================================================\n   Component: Button\n ========================================================================== */\n/*\n * 1. Remove margins in Chrome, Safari and Opera.\n * 2. Remove borders for `button`.\n * 3. Address `overflow` set to `hidden` in IE.\n * 4. Correct `font` properties and `color` not being inherited for `button`.\n * 5. Remove the inheritance of text transform in Edge, Firefox, and IE.\n * 6. Remove default style for `input type=\"submit\"`in iOS.\n * 7. Style\n * 8. `line-height` is used to create a height because it also centers the text vertically for `a` elements.\n *    Better would be to use height and flexbox to center the text vertically but flexbox doesn't work in Firefox on `button` elements.\n * 9. Align text if button has a width\n * 10. Required for `a`.\n */\n.uk-button {\n  /* 1 */\n  margin: 0;\n  /* 2 */\n  border: none;\n  /* 3 */\n  overflow: visible;\n  /* 4 */\n  font: inherit;\n  color: inherit;\n  /* 5 */\n  text-transform: none;\n  /* 6 */\n  -webkit-appearance: none;\n  border-radius: 0;\n  /* 7 */\n  display: inline-block;\n  box-sizing: border-box;\n  padding: 0 30px;\n  vertical-align: middle;\n  font-size: 0.875rem;\n  /* 8 */\n  line-height: 38px;\n  /* 9 */\n  text-align: center;\n  /* 10 */\n  text-decoration: none;\n  text-transform: uppercase;\n  transition: 0.1s ease-in-out;\n  transition-property: color, background-color, border-color;\n}\n\n.uk-button:not(:disabled) {\n  cursor: pointer;\n}\n\n/*\n * Remove the inner border and padding in Firefox.\n */\n.uk-button::-moz-focus-inner {\n  border: 0;\n  padding: 0;\n}\n\n/* Hover */\n.uk-button:hover {\n  /* 9 */\n  text-decoration: none;\n}\n\n/* Focus */\n.uk-button:focus {\n  outline: none;\n}\n\n/* OnClick + Active */\n/* Style modifiers\n ========================================================================== */\n/*\n * Default\n */\n.uk-button-default {\n  background-color: transparent;\n  color: #333;\n  border: 1px solid #e5e5e5;\n}\n\n/* Hover + Focus */\n.uk-button-default:hover,\n.uk-button-default:focus {\n  background-color: transparent;\n  color: #333;\n  border-color: #b2b2b2;\n}\n\n/* OnClick + Active */\n.uk-button-default:active,\n.uk-button-default.uk-active {\n  background-color: transparent;\n  color: #333;\n  border-color: #999999;\n}\n\n/*\n * Primary\n */\n.uk-button-primary {\n  background-color: #1e87f0;\n  color: #fff;\n  border: 1px solid transparent;\n}\n\n/* Hover + Focus */\n.uk-button-primary:hover,\n.uk-button-primary:focus {\n  background-color: #0f7ae5;\n  color: #fff;\n}\n\n/* OnClick + Active */\n.uk-button-primary:active,\n.uk-button-primary.uk-active {\n  background-color: #0e6ecd;\n  color: #fff;\n}\n\n/*\n * Secondary\n */\n.uk-button-secondary {\n  background-color: #222;\n  color: #fff;\n  border: 1px solid transparent;\n}\n\n/* Hover + Focus */\n.uk-button-secondary:hover,\n.uk-button-secondary:focus {\n  background-color: #151515;\n  color: #fff;\n}\n\n/* OnClick + Active */\n.uk-button-secondary:active,\n.uk-button-secondary.uk-active {\n  background-color: #090909;\n  color: #fff;\n}\n\n/*\n * Danger\n */\n.uk-button-danger {\n  background-color: #f0506e;\n  color: #fff;\n  border: 1px solid transparent;\n}\n\n/* Hover + Focus */\n.uk-button-danger:hover,\n.uk-button-danger:focus {\n  background-color: #ee395b;\n  color: #fff;\n}\n\n/* OnClick + Active */\n.uk-button-danger:active,\n.uk-button-danger.uk-active {\n  background-color: #ec2147;\n  color: #fff;\n}\n\n/*\n * Disabled\n * The same for all style modifiers\n */\n.uk-button-default:disabled,\n.uk-button-primary:disabled,\n.uk-button-secondary:disabled,\n.uk-button-danger:disabled {\n  background-color: transparent;\n  color: #999;\n  border-color: #e5e5e5;\n}\n\n/* Size modifiers\n ========================================================================== */\n.uk-button-small {\n  padding: 0 15px;\n  line-height: 28px;\n  font-size: 0.875rem;\n}\n\n.uk-button-large {\n  padding: 0 40px;\n  line-height: 53px;\n  font-size: 0.875rem;\n}\n\n/* Text modifiers\n ========================================================================== */\n/*\n * Text\n * 1. Reset\n * 2. Style\n */\n.uk-button-text {\n  /* 1 */\n  padding: 0;\n  line-height: 1.5;\n  background: none;\n  /* 2 */\n  color: #333;\n  position: relative;\n}\n.uk-button-text::before {\n  content: \"\";\n  position: absolute;\n  bottom: 0;\n  left: 0;\n  right: 100%;\n  border-bottom: 1px solid #333;\n  transition: right 0.3s ease-out;\n}\n\n/* Hover + Focus */\n.uk-button-text:hover,\n.uk-button-text:focus {\n  color: #333;\n}\n.uk-button-text:hover::before,\n.uk-button-text:focus::before {\n  right: 0;\n}\n\n/* Disabled */\n.uk-button-text:disabled {\n  color: #999;\n}\n.uk-button-text:disabled::before {\n  display: none;\n}\n\n/*\n * Link\n * 1. Reset\n * 2. Style\n */\n.uk-button-link {\n  /* 1 */\n  padding: 0;\n  line-height: 1.5;\n  background: none;\n  /* 2 */\n  color: #1e87f0;\n}\n\n/* Hover + Focus */\n.uk-button-link:hover,\n.uk-button-link:focus {\n  color: #0f6ecd;\n  text-decoration: underline;\n}\n\n/* Disabled */\n.uk-button-link:disabled {\n  color: #999;\n  text-decoration: none;\n}\n\n/* Group\n ========================================================================== */\n/*\n * 1. Using `flex` instead of `inline-block` to prevent whitespace betweent child elements\n * 2. Behave like button\n * 3. Create position context\n */\n.uk-button-group {\n  /* 1 */\n  display: inline-flex;\n  /* 2 */\n  vertical-align: middle;\n  /* 3 */\n  position: relative;\n}\n\n/* Group\n ========================================================================== */\n/*\n * Collapse border\n */\n.uk-button-group > .uk-button:nth-child(n+2),\n.uk-button-group > div:nth-child(n+2) .uk-button {\n  margin-left: -1px;\n}\n\n/*\n * Create position context to superimpose the successor elements border\n * Known issue: If you use an `a` element as button and an icon inside,\n * the active state will not work if you click the icon inside the button\n * Workaround: Just use a `button` or `input` element as button\n */\n.uk-button-group .uk-button:hover,\n.uk-button-group .uk-button:focus,\n.uk-button-group .uk-button:active,\n.uk-button-group .uk-button.uk-active {\n  position: relative;\n  z-index: 1;\n}\n\n/* ========================================================================\n   Component: Section\n ========================================================================== */\n/*\n * 1. Make it work with `100vh` and height in general\n */\n.uk-section {\n  box-sizing: border-box;\n  /* 1 */\n  padding-top: 40px;\n  padding-bottom: 40px;\n}\n\n/* Desktop and bigger */\n@media (min-width: 960px) {\n  .uk-section {\n    padding-top: 70px;\n    padding-bottom: 70px;\n  }\n}\n/*\n * Micro clearfix\n */\n.uk-section::before,\n.uk-section::after {\n  content: \"\";\n  display: table;\n}\n\n.uk-section::after {\n  clear: both;\n}\n\n/*\n * Remove margin from the last-child\n */\n.uk-section > :last-child {\n  margin-bottom: 0;\n}\n\n/* Size modifiers\n ========================================================================== */\n/*\n * XSmall\n */\n.uk-section-xsmall {\n  padding-top: 20px;\n  padding-bottom: 20px;\n}\n\n/*\n * Small\n */\n.uk-section-small {\n  padding-top: 40px;\n  padding-bottom: 40px;\n}\n\n/*\n * Large\n */\n.uk-section-large {\n  padding-top: 70px;\n  padding-bottom: 70px;\n}\n\n/* Tablet landscape and bigger */\n@media (min-width: 960px) {\n  .uk-section-large {\n    padding-top: 140px;\n    padding-bottom: 140px;\n  }\n}\n/*\n * XLarge\n */\n.uk-section-xlarge {\n  padding-top: 140px;\n  padding-bottom: 140px;\n}\n\n/* Tablet landscape and bigger */\n@media (min-width: 960px) {\n  .uk-section-xlarge {\n    padding-top: 210px;\n    padding-bottom: 210px;\n  }\n}\n/* Style modifiers\n ========================================================================== */\n/*\n * Default\n */\n.uk-section-default {\n  background: #fff;\n}\n\n/*\n * Muted\n */\n.uk-section-muted {\n  background: #f8f8f8;\n}\n\n/*\n * Primary\n */\n.uk-section-primary {\n  background: #1e87f0;\n}\n\n/*\n * Secondary\n */\n.uk-section-secondary {\n  background: #222;\n}\n\n/* Overlap modifier\n ============================