utline: none;\n}\n\n/* ========================================================================\n   Component: Label\n ========================================================================== */\n.uk-label {\n  display: inline-block;\n  padding: 0 10px;\n  background: #1e87f0;\n  line-height: 1.5;\n  font-size: 0.875rem;\n  color: #fff;\n  vertical-align: middle;\n  white-space: nowrap;\n  border-radius: 2px;\n  text-transform: uppercase;\n}\n\n/* Color modifiers\n ========================================================================== */\n/*\n * Success\n */\n.uk-label-success {\n  background-color: #32d296;\n  color: #fff;\n}\n\n/*\n * Warning\n */\n.uk-label-warning {\n  background-color: #faa05a;\n  color: #fff;\n}\n\n/*\n * Danger\n */\n.uk-label-danger {\n  background-color: #f0506e;\n  color: #fff;\n}\n\n/* ========================================================================\n   Component: Overlay\n ========================================================================== */\n.uk-overlay {\n  padding: 30px 30px;\n}\n\n/*\n * Remove margin from the last-child\n */\n.uk-overlay > :last-child {\n  margin-bottom: 0;\n}\n\n/* Icon\n ========================================================================== */\n/* Style modifiers\n ========================================================================== */\n/*\n * Default\n */\n.uk-overlay-default {\n  background: rgba(255, 255, 255, 0.8);\n}\n\n/*\n * Primary\n */\n.uk-overlay-primary {\n  background: rgba(34, 34, 34, 0.8);\n}\n\n/* ========================================================================\n   Component: Article\n ========================================================================== */\n/*\n * Micro clearfix\n */\n.uk-article::before,\n.uk-article::after {\n  content: \"\";\n  display: table;\n}\n\n.uk-article::after {\n  clear: both;\n}\n\n/*\n * Remove margin from the last-child\n */\n.uk-article > :last-child {\n  margin-bottom: 0;\n}\n\n/* Adjacent sibling\n ====================