/ Adapted from http://github.com/h5bp/html5-boilerplate\n//\n// Modifications:   Removed link `href` and `title` related rules\n//\n// ========================================================================\n\n\n/* ========================================================================\n   Component: Print\n ========================================================================== */\n\n@media print {\n\n    *,\n    *::before,\n    *::after {\n        background: transparent !important;\n        color: black !important;\n        box-shadow: none !important;\n        text-shadow: none !important;\n    }\n\n    a,\n    a:visited { text-decoration: underline; }\n\n    pre,\n    blockquote {\n        border: 1px solid #999;\n        page-break-inside: avoid;\n    }\n\n    thead { display: table-header-group; }\n\n    tr,\n    img { page-break-inside: avoid; }\n\n    img { max-width: 100% !important; }\n\n    @page { margin: 0.5cm; }\n\n    p,\n    h2,\n    h3 {\n        orphans: 3;\n        widows: 3;\n    }\n\n    h2,\n    h3 { page-break-after: avoid; }\n\n   @if(mixin-exists(hook-print)) {@include hook-print();}\n\n}\n\n// Hooks\n// ========================================================================\n\n// @mixin hook-print(){}\n","// Import all the theming functionality.\n// We want overlays to always appear over user content, so set a baseline\n// very high z-index for the overlay container, which is where we create the new\n// stacking context for all overlays.\n$cdk-z-index-overlay-container: 1000 !default;\n$cdk-z-index-overlay: 1000 !default;\n$cdk-z-index-overlay-backdrop: 1000 !default;\n\n// Background color for all of the backdrops\n$cdk-overlay-dark-backdrop-background: rgba(0, 0, 0, 0.32) !default;\n\n// Default backdrop animation is based on the Material Design swift-ease-out.\n$backdrop-animation-duration: 400ms !default;\n$backdrop-animation-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1) !default;\n\n\n@mixin cdk-overlay() {\n  .cdk-overlay-c